.todo-item {
    padding:1rem;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align: center;
        align-items: center;
    cursor:pointer;
    -webkit-transition: all 0.15s;
    -o-transition: all 0.15s;
    transition: all 0.15s;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
}

.todo-item:hover {
    background-color:#e3fafc;
}

.todo-item:hover .remove {
    opacity: 1;
}

.todo-item + .todo-item {
    border-top: 1px solid #f1f3f5;
}

.remove {
    margin-right: 1rem;
    color: #e64980;
    font-weight: 600;
    opacity: 0;
}

.todo-text {
    -ms-flex:1 1;
        flex:1 1;
    word-break: break-all;
}

.checked {
    text-decoration: line-through;
    color: #adb5bd;
}

.check-mark {
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
    color: #3bc9db;
    font-weight: 800;    
}



