.routine-item{
    padding:1rem;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transition: all 0.15s;
    -o-transition: all 0.15s;
    transition: all 0.15s;
}

.routine-item:hover{
    background-color:#e9fac8;
}

.routine-item:hover .remove {
    opacity: 1;
}

.routine-item + .routine-item{
    border-top:1px solid #c0eb75;
}

.remove {
    margin-right: 1rem;
    color: #e64980;
    font-weight: 600;
    opacity: 0;
}



